.home-page {
    .text-zone {
        position: absolute;
        left: 10%;
        top: 45%;
        transform: translateY(-50%);
        width: 40%;
        max-height: 70%;
    }

    h1 {
        color: #fff;
        font-size: 3vw;
        margin-top: 1vw;
        font-family: 'Coolvetica';
        font-weight: 400;
        line-height: 3vw;
        white-space: nowrap;

        .name {
            font-size: 8vw;
            line-height: 8vw;
        }

        .letter {
            display: inline-block;

            &:hover {
                animation: jello 1s;
                color: #8BD8BD;
            }
        }

        .typed {
            color: #8BD8BD;
        }
    }

    h2 {
        color: #ddd;
        margin-top: 1vw;
        font-weight: 400;
        font-size: 1.5vw;
        animation: fadeIn 1s 1.8s backwards;
    }

    .flat-button {
        color: #8BD8BD;
        font-size: 1.5vw;
        font-weight: 100;
        font-family: 'Coolvetica';
        letter-spacing: 4px;
        text-decoration: none;
        padding: 10px 18px;
        border: 1px solid #8BD8BD;
        margin-top: 15px;
        float: left;
        animation: fadeIn 1s 1.8s backwards;
        transition: all 0.3s ease-in;
        white-space: nowrap;

        &:hover {
            background: #8BD8BD;
            color: #333;
        }
    }

}

@media only screen and (orientation: portrait) {
    .home-page {
        .text-zone {
            left: 25%;
            top: 67%;

            h1 {
                font-size: 4.5vw;
                line-height: 4.5vw;

                .name {
                    font-size: 12.5vw;
                    line-height: 12.5vw;
                }
            }

            h2 {
                font-size: 4vw;
            }

            .flat-button {
                font-size: 4vw;
                margin-top: 0;
            }

        }
    }
}

@media only screen and (orientation: portrait) and (min-width: 700px) {
    .home-page {
        .text-zone {
            left: 25%;
            top: 70%;

            h1 {
                font-size: 3.5vw;
                line-height: 3.5vw;

                .name {
                    font-size: 9.5vw;
                    line-height: 9.5vw;
                }
            }

            h2 {
                font-size: 2vw;
            }

            .flat-button {
                font-size: 2vw;
                margin-top: 0;
            }

        }
    }
}