.skills-page {
    .text-zone {
        position: absolute;
        left: 10%;
        top: 30%;
        transform: translateY(-50%);
        width: 40%;
        max-height: 70%;
    }

    h1 {
        color: #8BD8BD;
        font-size: 4vw;
        margin-top: 1vw;
        font-family: 'Coolvetica';
        font-weight: 400;
        line-height: 4vw;
        white-space: nowrap;
    
    }

    .letter {
        display: inline-block;

        &:hover {
            animation: jello 1s;
            color: #fff;
        }
    }

    p {
        color: #ddd;
        margin-top: 1vw;
        font-weight: 400;
        font-size: 1vw;
        animation: fadeIn 1s 1.8s backwards;
    }
}


@media only screen and (orientation: portrait) {
    .skills-page {
        .text-zone {
            left: 35%;
            top: 35%;

            h1 {
                font-size: 10vw;
                line-height: 10vw;
            }

            p {
                font-size: 4vw;
            }
        }
    }
}

@media only screen and (orientation: portrait) and (min-width: 700px) {
    .skills-page {
        .text-zone {
            left: 25%;
            top: 35%;
        }
    }
}
