$primary-color: #8BD8BD;

@import 'animate.css';
@import '~loaders.css/src/animations/ball-triangle-trace.scss';

@font-face {
  font-family: 'Helvetica Neue';
  src: url('./assets/fonts/helvetica-neu.ttf') format('ttf');
}

@font-face {
  font-family: 'La Belle Aurore';
  src: url('./assets/fonts/LaBelleAurore.woff2') format('woff2');
}

@font-face {
  font-family: 'Coolvetica';
  src: url('./assets/fonts/CoolveticaRg-Regular.woff2') format('woff2');
}

@font-face {
  font-family: 'Source Code Pro';
  src: url('./assets/fonts/SourceCodePro-Regular.ttf') format('ttf');
}

input,
textarea {
  font-family: 'Helvetica Neue';
}

.loader-active {
  display: block;
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  top: 0;
  bottom: 0;
  width: 50px;
  height: 50px;
  animation: fadeOut 0.25s 0.75s forwards;
}

@media only screen and (orientation: portrait) {
  .loader-active {
    left: calc(30vw - 25px);
  }
}

@media only screen and (orientation: portrait) and (min-width: 700px) {
  .loader-active {
    left: calc(20vw - 25px);
  }
}