.headshot-container {
    z-index: 0;
    width: 35vw;
    height: 39.5vw;
    position: absolute;
    top: 11%;
    right: 10%;
    left: auto;
    margin: auto;
    white-space: nowrap;

    svg {
        width: 35vw;
        height: 39.5vw;
    }

    .solid-headshot {
        position: absolute;
        top: 3vw;
        right: auto;
        bottom: auto;
        left: 3vw;
        margin: auto;
        width: 29vw;
    }
}

.line {
    stroke-dasharray: 310;
    stroke-dashoffset: 300;
    animation: dash 4s ease-out forwards;
}

@keyframes dash {
    to {
        stroke-dashoffset: 0;
    }
}

@media only screen and (orientation: portrait) {
    .headshot-container {
        width:  26.6vh;
        height: 30vh;
        display: block;
        right: 20%;
        left: 33%;
        top: 11%;

        svg {
            width:  26.6vh;
            height: 30vh;
    
        }

        .solid-headshot {
            top: 2.2vh;
            left: 2.2vh;
            width: 22vh;


        }
    }
}