.page {
    width: 100%;
    height: 100%;
    position: absolute;
}

.top-tags {
    bottom: auto;
    top: 25px;
}

.tags {
    color: #8BD8BD;
    opacity: 0.6;
    position: absolute;
    bottom: 20px;
    left: 120px;
    font-size: 18px;
    font-family:'Source Code Pro';
}

.top-tag-html {
    margin-left: -30px;
}

.bottom-tag-html {
    margin-left: -30px;
}

.container {
    width: 100%;
    will-change: contents;
    height: 90%;
    min-height: 566px;
    position: absolute;
    opacity: 0;
    top: 5%;
    margin: 0 auto;
    z-index: 1;
    transform-style: preserve-3d;
    animation: fadeIn 1s forwards;
    animation-delay: 1s;
}